.Hero{
    background-image: url(../../img/bg-header2.png);
    height: 100vh;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    @include responsive(hd){
        background-image: none;
        padding-top: 8em;
    }


    // Image For Desktop
    &_desktop{
        display: flex;
        flex-direction: column;
        gap: .5em;
        text-align: center;
        width: 90%;
        justify-content: center;
        align-items: center;

        @include responsive(hd){
            background-image: url(../../img/bg-header.png);
            height: 100vh;
            background-size: cover;
            background-position: center;
        }

        h6{
            font-size: 48px;
            font-weight: 800;
            @include responsive(s){
                font-size: 72px;
            }
            @include responsive(xl){
                font-size: 96px;
            }

            @include responsive(hd){
                font-size: 144px;
            }
        }
    }
}


h1{
    background-color: brown;
    height: 20em;
    margin-top: 5em;
}

